<script>
import { mapState } from 'vuex'
export default {
  computed: {
    ...mapState({
      english: states => states.english
    })
  }
}
</script>

<template>
  <div>
    <div v-if="english">
      404 error
    </div>
    <div v-else>
      404 报错
    </div>
  </div>
</template>

